.todoItem{padding:20px;font-size:24px;display:flex;align-items:center;gap:30px;position:relative}.todoItem:hover .delete{opacity:1}.todoItem:not(:last-child){border-bottom:1px solid #d9d9d9}.completed{text-decoration:line-through;color:#d9d9d9}.checkbox input{appearance:none;display:none}.checkbox input:checked~.toggle{border-color:#bddad5}.checkbox input:checked~.toggle::before{content:"âœ“";position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:#5dc2af;font-size:18px}.toggle{width:30px;height:30px;border:1px solid #d9d9d9;border-radius:50%;cursor:pointer;position:relative}.delete{background:none;outline:none;border:none;color:#cc9a9a;cursor:pointer;font-size:22px;font-weight:600;position:absolute;right:25px;top:50%;transform:translateY(-50%);transition:color .3s,opacity .3s;opacity:0}.delete:hover{color:#af5b5e}